import react, { Component } from 'react'
import { Link } from 'react-router-dom'
import axios from 'axios'
import Load from '../layout/Load'
import { object } from 'prop-types'
class Lyrictrack extends Component {
    state = {
        track: {},
        Lyrics: {}
    }
    componentDidMount() {
        console.log()
        axios.get(`https://cors-anywhere.herokuapp.com/https://api.musixmatch.com/ws/1.1/track.lyrics.get?track_id=${this.props.match.params.id}&apikey=e14a45ccf2f5505b91cae724613bf919`).then((res) => {
            this.setState({ Lyrics: res.data.message.body.lyrics })
            console.log(this.state)

        })

        axios.get(`https://cors-anywhere.herokuapp.com/https://api.musixmatch.com/ws/1.1/track.get?track_id=${this.props.match.params.id}&apikey=e14a45ccf2f5505b91cae724613bf919`).then((res) => {

            this.setState({ track: res.data.message.body.track })
        })

    } track
    render() {
        const { track, Lyrics } = this.state
        console.log(track.length)

        if (track === undefined) {
            return (<div><Load />
                {console.log('loder')}
            </div>)
        }
        else
            return (
                <react.Fragment>
                    <Link to="/" className="btn btn-dark btn-sm mb-4">Go Back</Link>
                    <div className="card">

                        <div className="card-header">
                            {track.track_name} By  <span className="text-secondary">{track.artist_name}</span>
                        </div>
                        <div className="card-body">
                            <p className="card-body">{Lyrics.lyrics_body}</p>
                        </div>
                        <ul className="list-group mt-3">
                            <li className="list-item">
                                Album id <strong>{track.album_id}</strong>
                            </li>
                            <li className="list-item">
                                {/* Album Name <strong> :{track.primary_genres.music_genre_list[0].
                                music_genre.music_genre_name}</strong> */}
                            </li>
                        </ul>
                    </div>
                </react.Fragment>

            )



    }
}

export default Lyrictrack;