import react, { Component } from 'react'
import { Consumer } from '../Context';
import Trackdata from './layout/Trackdata';


class Track extends Component {

    render() {

        return (
            <Consumer>
                {
                    Value => {
                        const { track_list,Header }= Value
                        if (track_list === undefined || track_list.length === 0) {

                            console.log(Value)   
                            
                        }
                        else {
                            // console.log(Header)
                            // console.log(track_list)   
                            return (
                                <react.Fragment>
                                    <div className="row">
                                        {
                                            track_list.map((item)=>(
                                                <Trackdata key={item.track.track_id } track={item.track} />
                                            ))
                                        }
                                    </div>
                                </react.Fragment>
                            )
                        }
                    }
                }
                
            </Consumer>
        )
    }
}
export default Track;