
import './App.css';
import Navbar from './Componenet/layout/Navbar'
import { BrowserRouter as Router, Route, Link,Switch } from 'react-router-dom'
import React from 'react';
import Data from './Componenet/layout/Data';
import {Provider} from './Context';
import Lyrictrack from './Componenet/Lyrics/Lyrictrack';
function App() {
  return (
<Provider>
    <Router>
      <React.Fragment>

        <Navbar />
        <div className="container">
          <Switch>
            <Route exact path="/" component={Data} ></Route>
            <Route exact path="/Lyrics/Lyrictrack/:id" component={Lyrictrack} ></Route>
          </Switch>
        </div>
      </React.Fragment>
    </Router>
    </Provider>


  );
}

export default App;
