import React, { Component } from 'react'
import axios from 'axios';
const Context = React.createContext()
export class Provider extends Component {

    state = {
        track_list: [],
        Header: 'Top 10 Song List'
    };

    componentDidMount() {
        axios.get('https://cors-anywhere.herokuapp.com/https://api.musixmatch.com/ws/1.1/chart.tracks.get?chart_name=top&page=1&page_size=10&country=india&f_has_lyrics=1&apikey=e14a45ccf2f5505b91cae724613bf919').then((res) => {
            
             this.setState({track_list:res.data.message.body.track_list})
             console.log(this.state)
        })
    }
    render() {
        return (
            <Context.Provider value={this.state}>
                {this.props.children}
            </Context.Provider>

        )
    }
}

export const Consumer = Context.Consumer